/* Reset margins, padding, and box sizing */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: #333;
  position: relative;
  min-height: 100vh;
  background-image: url('background.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.5); /* White overlay with 50% opacity */
  z-index: 0;
}

header {
  background-color: #004d99;
  color: #fff;
  padding: 20px 40px;
}

header h1 {
  margin: 0;
  font-size: 24px;
}

nav {
  margin-top: 10px;
}

nav a {
  color: #fff;
  background-color: #4CAF50;
  padding: 10px 20px;
  margin-right: 10px;
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
}

nav a:hover {
  background-color: #005cbf;
}


.card {
  background-color: #f4f4f4;
  padding: 20px;
  width: 48%; /* Adjusted for flexbox use if needed */
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.card h2 {
  color: #004d99;
}

/* Ensure other content is above the overlay */
.containerr, header, nav {
  position: relative;
  z-index: 1;
}
.containerr {
  display: flex;
  justify-content: space-between; /* Adjusts spacing between the cards */
  flex-wrap: wrap; /* Allows cards to wrap on smaller screens */
}

.card {
  flex-basis: 48%; /* Each card will take roughly half of the container width */
  margin: 1%; /* Small margin for spacing */
}
