
/* CheckPlagiarismPage.css */
.cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 1;
}

.Attach {
    cursor: pointer;
    background-color:#4CAF50;
    color: white;
    padding: 10px 20px;
    border-radius: 9px;
    transition: background-color 0.3s;
}
.Attach:hover{
    background: #005cbf;
}

.text-red-500 {
    color: #d9534f;
}

.text-green-500 {
    color: #5cb85c;
}
.file-listPlag {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    border-radius: 4px;
    padding: 8px;
    max-height: 400px; /* Increase the maximum height to display more content */
    overflow-y: auto; /* Maintain vertical scrolling */
    background-color: #f9f9f9; /* Light background */
    border: 1px solid #ccc; /* Subtle border */
    box-shadow: 0 2px 5px rgba(0,0,0,0.1); /* Soft shadow to lift the container visually */
    width: 95%; /* Increase width to utilize more space */
    margin: 20px auto; /* Centering the file list */
}


.file-namePlag {
    background-color: #fff;
    border: 1px solid #cbd5e0;
    border-radius: 4px;
    padding: 8px 12px;
    display: flex;
    align-items: center;
    font-size: 14px; /* Ensuring readability */
}

.submit-button{
    background-color: #4CAF50; /* Green color for delete button */
    color: white; /* White text for better contrast */
    padding: 10px 20px; /* Increase padding for a larger button */
    font-size: 16px; /* Larger font size for better readability */
    border-radius: 8px; /* Rounded corners for a softer look */
    border: none; /* Remove border */
    cursor: pointer; /* Cursor changes to pointer to indicate clickable */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Soft shadow for 3D effect */
    transition: background-color 0.3s, box-shadow 0.3s; /* Smooth transition for hover effects */
  }